$comment-border-width: 1em;
$comment-border-color--advice: #02C39A;
$comment-border-color--info: #5C9EAD;
$comment-border-color--warning: #F79D65;

html {
    margin: 0px;
    padding: 0px;
    min-height: 100%;
    height: 100%;
    --chrome—color-light0: #ffffff;
    --chrome—color-light1: #fafafa;
    --chrome—color-light2: #f8f8f8;
    --chrome—color-dark0: #121212;
    --chrome—color-dark1: #171727;
    --chrome—color-dark2: #1f1f2f;

    --theme-style: 'retro';

    --text--color-base--light:            22, 21, 30;
    --background--color-base--light:      255,255,255;

    --text--color-base--dark:             254, 248, 239;
    --background--color-base--dark:       22, 21, 30;

    --systemBlueLight:   #0051a9;
    --systemGrayLight:   rgb(142,142,147);
    --systemGreenLight:  #008221;
    --systemIndigoLight: rgb(88,86,214);
    --systemOrangeLight: rgb(255,149,0);
    --systemPinkLight:   rgb(255,45,85);
    --systemPurpleLight: rgb(175,82,222);
    --systemRedLight:    #e00d01;
    --systemTealLight:   #0075ab;
    --systemYellowLight: rgb(255,204,0);

    --systemBlueDark:    #51a8ff;
    --systemGrayDark:    rgb(152,152,157);
    --systemGreenDark:   rgb(48,209,88);
    --systemIndigoDark:  #9a99ff;
    --systemOrangeDark:  rgb(255,159,10);
    --systemPinkDark:    rgb(255,55,95);
    --systemPurpleDark:  #fd6fce;
    --systemRedDark:     rgb(255,69,58);
    --systemTealDark:    rgb(100,210,255);
    --systemYellowDark:  rgb(255,214,10);

    --text-base: 0,0,0;
    --text: rgba(var(--text-base),1);

    --background-base: 0,0,0;
    --background: rgba(var(--background-base), 1);

    --font: '';

    font-size: 1em;

    --border-radius: 50px;

    &[theme-style='retro'] {
        --theme-style: 'retro';
        --font: 'pxplus_ibm_vga8regular';
        --border-radius: 0px; }

    &[theme-style='modern'] {
        --theme-style: 'modern';
        --border-radius: 50px;
        --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; } }

html[theme="light"] {

    --primary:   var(--chrome—color-light0);
    --secondary: var(--chrome—color-light1);
    --third:     var(--chrome—color-light2);

    --systemBlue:   var(--systemBlueLight);
    --systemGray:   var(--systemGrayLight);
    --systemGreen:  var(--systemGreenLight);
    --systemIndigo: var(--systemIndigoLight);
    --systemOrange: var(--systemOrangeLight);
    --systemPink:   var(--systemPinkLight);
    --systemPurple: var(--systemPurpleLight);
    --systemRed:    var(--systemRedLight);
    --systemTeal:   var(--systemTealLight);
    --systemYellow: var(--systemYellowLight);

    --background-color--selection: yellow;

    &[theme-style='retro'] {
        --text-base:        var(--text--color-base--light);
        --background-base:  var(--background--color-base--light); }

    &[theme-style='modern'] {
        --text-base: 50,50,60;
        --background-base: var(--primary); } }

html[theme='dark'] {

    --primary:   var(--chrome—color-dark0);
    --secondary: var(--chrome—color-dark1);
    --third:     var(--chrome—color-dark2);

    --systemBlue:   var(--systemBlueDark);
    --systemGray:   var(--systemGrayDark);
    --systemGreen:  var(--systemGreenDark);
    --systemIndigo: var(--systemIndigoDark);
    --systemOrange: var(--systemOrangeDark);
    --systemPink:   var(--systemPinkDark);
    --systemPurple: var(--systemPurpleDark);
    --systemRed:    var(--systemRedDark);
    --systemTeal:   var(--systemTealDark);
    --systemYellow: var(--systemY ellowDark);

    --background-color--selection: red;

    &[theme-style='retro'] {
        --text-base:       var(--text--color-base--dark);
        --background-base: var(--background--color-base--dark); }

    &[theme-style='modern'] {
        --text-base: 245,245,245;
        --background: var(--primary); } }

body, p, b, button {
    // font-smooth: never
    // -webkit-font-smoothing : none
    text-align: center;
    line-height: 1.5;
    font-size: 1em;
    font-family: var(--font);
    color: var(--text);
    background: var(--background); }

::selection {
    color: var(--text-color--selection);
    background-color: var(--background-color--selection); }

button {
    border: none;
    color: rgba(var(--text-base), .6);
    &:hover {
        color: rgba(var(--text-base), 1); }
    &:focus {
        outline: 0; } }

img {
    max-width: 200px;
    border-radius: 50px; }

.faint {
    color: rgba(var(--text-base), .5); }

a {
    color: var(--text);
    opacity: 0.8;
    text-decoration: none;
    font-family: var(--font);
    &:hover {
        opacity: 1.0;
        color: var(--text); } }

hr {
    border-color: rgba(var(--text-base), 0.1);
    border-width: 0.5px;
    margin: 0 auto;
    margin-top: 20px;
    width: 60%; }

p {
    background: none; }

span {
    color: var(--text);
    font-family: var(--font); }

ul {
    padding-inline-start: 0px; }
li {
    margin: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
    list-style-type: none; }

.bold {
    font-weight: 500; }

.list {
    margin: 0;
    padding: 0;
    text-align: left;
    display: inline-block;
    h2 {
        margin: 5px;
        text-align: left;
        font-size: 1.5em;
        font-weight: 700; }

    @media only screen and (max-width: 800px) {
        margin: 0;
        padding: 0;
        font-size: 1.5em; } }
a {
    padding: 0;
    margin: 0; }

.list--item {
    display: flex;
    opacity: 0.0;
    animation: reveal 1s forwards; }

.list--item-title {
    font-size: 1em;
    padding: 0;
    margin: 0;
    margin-right: 5px;
    text-align: left; }

.badge {
    display: inline-block;
    margin-left: 5px;

    background: var(--third);
    padding: 10px;
    padding-top: 2px;
    padding-bottom: 2px;
    border-radius: var(--border-radius);
    font-size: 0.75em;
    font-weight: 400; }

.badge--item {
    margin-left: 5px;
    margin-right: 5px; }

@keyframes reveal {
    from {
        opacity: 0.0; }
    to {
        opacity: 1.0; } }
